/* Import GeistSans font */
@import url("https://fonts.googleapis.com/css2?family=GeistSans:wght@300;400;500;600&display=swap");

/* Basic reset */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Apply GeistSans font globally */
body,
form,
label,
input,
select,
button,
h2,
h3 {
  font-family: "GeistSans", sans-serif;
}

body {
  /* background-color: #f0f2f5; */

  background-color: #f6f3ff;
  color: #333;
  line-height: 1.6;
}

/* Container styling */
.resume-form {
  max-width: 800px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

h2 {
  font-size: 1.8rem;
  font-weight: 600;
  color: #3682f4;
  margin-bottom: 1.5rem;
  text-align: center;
}

h3 {
  font-size: 1.4rem;
  font-weight: 700;
  color: #0f151c;
  margin-bottom: 1.2rem;
  border-bottom: 2px solid #ecf0f1;
  padding-bottom: 0.5rem;
}

.form-section {
  margin-bottom: 2rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 550;
  color: #323234;
}

/* Input styling */
input[type="text"],
input[type="email"],
input[type="tel"],
select {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #cbd5e0;
  border-radius: 6px;
  font-size: 1rem;
  color: #2d3748;
  transition: all 0.3s ease;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
select:focus {
  border-color: #8242e1;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.15);
  outline: none;
}

textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #cbd5e0;
  border-radius: 6px;
  font-size: 1rem;
  color: #2d3748;
  transition: all 0.3s ease;
  resize: vertical;
  min-height: 100px;
}

textarea:focus {
  border-color: #8242e1;
  box-shadow: 0 0 0 3px rgba(66, 153, 225, 0.15);
  outline: none;
}

textarea::placeholder {
  color: #b0bdcf;
}

/* Placeholder styling */
input::placeholder {
  color: #b0bdcf;
}

/* Checkbox */
input[type="checkbox"] {
  margin-right: 0.5rem;
}

/* Date inputs */
.date-inputs {
  display: flex;
  gap: 1rem;
}

.date-inputs select {
  flex: 1;
}

/* Buttons styling */
button {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

button[type="button"] {
  background-color: #3682f4;
  color: #ffffff;
  border: none;
}

button[type="button"]:hover {
  background-color: #1b70ef;
}

button[type="submit"] {
  background-color: #14b356;
  color: #ffffff;
}

button[type="submit"]:hover {
  background-color: #00c351;
}

button.remove-btn {
  background-color: #f56565;
  color: #ffffff;
  padding: 0.5rem 1rem;
  font-size: 0.875rem;
}

button.remove-btn:hover {
  background-color: #e53e3e;
}

/* Disabled button styling */
button:disabled {
  background-color: #cbd5e0;
  color: #c0c6d1;
  cursor: not-allowed;
}

/* Form navigation */
.form-navigation {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}

/* Experience and Education items */
.experience-item,
.education-item {
  background-color: #fcfcff;
  border: 1px solid #b2b5ff;
  border-radius: 8px;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
}

/* Skills */
.skills-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.skill-item {
  display: flex;
  align-items: center;
  background-color: #ebf8ff;
  border-radius: 20px;
  padding: 0.5rem 1rem;
}

.skill-item input {
  border: none;
  background: transparent;
  margin-right: 0.5rem;
}

.skill-item button {
  background: none;
  border: none;
  color: #4299e1;
  cursor: pointer;
  font-size: 1.2rem;
  padding: 0;
}

/* Responsive design */
@media (max-width: 768px) {
  .resume-form {
    padding: 1.5rem;
    margin: 1rem;
  }

  .date-inputs {
    flex-direction: column;
    gap: 0.5rem;
  }

  button {
    width: 100%;
    margin-bottom: 0.5rem;
  }

  .form-navigation {
    flex-direction: column-reverse;
  }
}
