.template-selector {
  text-align: center;
}

.template-options {
  display: flex;
  flex-wrap: wrap; /* Allows wrapping of the elements */
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.template-option {
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  width: 280px; /* Maintain width */
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.template-option img {
  width: 100%;
  height: auto;
  display: block;
}

.template-option button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 0 0 8px 8px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.template-option:hover {
  transform: translateY(-5px);
  box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.1);
}

.template-option button:hover {
  background-color: #0056b3;
}

/* Responsive styling */
@media (max-width: 768px) {
  .template-option {
    width: 100%; /* Full width on smaller screens */
    max-width: 400px; /* Set a max width for better readability */
  }
}

@media (max-width: 480px) {
  .template-options {
    gap: 10px; /* Reduce the gap for smaller screens */
  }

  .template-option {
    width: 100%; /* Full width */
  }
}
