/* Base Styles */
button {
  border: solid 1px blue;
}
.template-switcher {
  display: flex;
  margin-bottom: 1rem;
  /* align-items: center; */
  justify-content: center;
}
.resume-preview {
  max-width: 800px;
  margin: 0 auto;
  /* padding: 20px; */
  font-family: "Arial", sans-serif;
  color: #333;
}

.resume-preview header {
  text-align: center;
  margin-bottom: 15px;
}

.resume-preview h1 {
  font-size: 26px;
  margin-bottom: 6px;
}

.resume-preview p {
  margin: 0;
  font-size: 14px;
}
.resume-preview p.summary {
  font-style: italic;
  font-size: smaller;
  padding-top: 10px;
}

.resume-preview section {
  margin-bottom: 10px;
  /* padding: 40px 20px; */
}

.resume-preview h2 {
  font-size: 16px;
  margin-bottom: 10px;
  color: #007bff;
  border-bottom: 2px solid #007bff;
  padding-bottom: 5px;
}

.resume-preview .experience-item,
.resume-preview .education-item {
  margin-bottom: 5px;
  padding: 0.5rem 1rem;
}

.resume-preview .experience-item h4,
.resume-preview .education-item h4 {
  font-size: 14px;
  margin-bottom: 5px;
  color: #444;
}

.resume-preview ul {
  list-style-type: none;
  padding: 0;
}

.resume-preview ul li {
  background-color: #f8f9fa;
  padding: 6px 10px;
  margin-bottom: 5px;
  border-radius: 4px;
  font-size: 14px;
  color: #555;
}

/* Media Query for Mobile Responsiveness */
@media (max-width: 600px) {
  .resume-preview {
    padding: 15px;
  }

  .resume-preview h1 {
    font-size: 24px;
  }

  .resume-preview h2 {
    font-size: 18px;
  }

  .resume-preview p,
  .resume-preview h4 {
    font-size: 12px;
  }
}

.resume-preview section.skills ul {
  padding-left: 20px;
}

