h1.product-name {
  position: sticky;
  text-align: center;
  background-color: #3682f4;
  color: whitesmoke;
  padding: 1rem;
  top: 0;
  z-index: 1000;
}

.action-buttons {
  display: flex;
  justify-content: center;
  margin-bottom: 3rem;
}

.action-buttons button {
  margin: 0 1rem;
  color: azure;
  font-weight: 800;
  border: none;
}

.action-buttons .download-button {
  background-color: #00a005;
}

.action-buttons .reset-button {
  background-color: #f44336;
  /* background-color: #f56565; */
}
